<template>
  <div >
     <header class="header">
       <div class="back"><i class="iconfont icon-xiangzuojiantou"></i></div>
       <div class="search"><i class="iconfont icon-gouwuchekong"></i><input type="text" placeholder="输入城市/景点/游玩主题" /></div>
       <div class="city" @click="handleCity">{{city}}<i class="iconfont icon-xiajiantou"></i></div>
     </header>
     <index-swiper :swiperInfo="swiperInfo"></index-swiper>
     <swiper :options="iconsOption">
         
       <swiper-slide v-for="(pageInfo,index) in pages" :key="index">
        <div class="icon-wrapper">
         <div v-for="item in pageInfo" :key="item.id" class="icon-item">
             
               <router-link :to="'/detail/'+item.id" tag="div" class="img-icon">
                <img  class="icon" :src="item.imgurl">
               </router-link>
             
         </div>
        </div>
       </swiper-slide>
       <div class="swiper-pagination"  slot="pagination"></div>
     </swiper>
     <scroller class="recon">
       
           <li>1</li>
           <li>2</li>
           <li>3</li>
           <li>4</li>
           <li>5</li>
      
     </scroller>
     <div @click="handleClick" ref="con">{{name}}</div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import Vue from 'vue'
import IndexSwiper from './Swiper'
import Scroller from 'components/scroller/scroller'
export default {
  name: 'Index',
  data () {
    return {
       swiperInfo:[],
       iconInfo:[],
        iconsOption:{
          pagination:'.swiper-pagination'
        },
        name:"dell"

    }
  },
  components:{
    IndexSwiper,
    Scroller
  },
 computed:{
     ...mapState(['city']),
    /*city(){
      return this.$store.getters.tripleCity
    }*/
    // ...mapGetters({city:'tripleCity'}),
    pages() {
      const pages=[]
      this.iconInfo.forEach((value,index)=>{
        let page=Math.floor(index / 8)
        if(!pages[page]){
          pages[page]=[]
        }
        pages[page].push(value)
      })
      return pages
    }

  },
  
  methods:{
    getIndexData:function(){
        this.$http.get('/api/index.json').then(
        this.handleDataSource.bind(this)
      )
    },
    handleDataSource:function(res){
      const body=res.body
      if(body && body.data && body.data.swiper){
       this.swiperInfo=body.data.swiper
       this.iconInfo=body.data.icons
      }
    },
    handleClick:function(){
       this.name="lee"
       this.$nextTick(()=>{
        console.log(this.$refs.con.innerHTML)
       })
       
    },
    handleCity(){
      this.$router.push("/city")
    }
  },
  created(){
    this.getIndexData()
  }

}
</script>

<style scoped>
   .header{
    display: flex;
    height: .86rem;
    background: #05bad5;
    color: #fff;
   }
   .header .iconfont{
    font-size: .20rem;
   }
   .back{ 
    width: .64rem;
    line-height: .86rem;
    text-align: center;

   }
   .search{
    flex: 1;
    display: flex;
    align-items: center;
    margin: .14rem .18rem;
    background: #fff;
    border-radius: .1rem;
   }
   .search .icon-gouwuchekong{
    margin: 0 .13rem;
    color:#999;
   }
   .search input{
    border: 0;
    border-radius: .1rem;
    outline: 0;
   }
   .search input::-webkit-input-placeholder{
    color:#999;
   }
   .city{
    line-height: .86rem;
    text-align: center;
    
   }
   .city i{
    margin-left: .05rem;
   }
   .icon-wrapper{
    display: flex;
    flex-wrap: wrap;
    

   }
   .icon-item{
    box-sizing: border-box;
    width: 25%;
    padding:.2rem .4rem;
   }
   .img-icon{
    width: 100%;
   }
   .icon{
    width: 100%;
   }
   .recon{
   	width: 100%;
    height: 2.08rem;
    overflow: hidden;
   }
   .recon li{
    padding:.4rem;
    background: #ccc;
    text-align: center; 
   }
</style>


